.loginformContainer {
    display: flex;
    /* justify-content: center !important; */
    height: 685px;
    background-color: rgba(0, 0, 0, 0.705);
    backdrop-filter: blur(5px);
    width: 500px;
}

.login-form-Container {
    display: flex;
    /* justify-content: center !important; */
    height: 765px;
    background-color: rgba(0, 0, 0, 0.705);
    backdrop-filter: blur(5px);
    width: 500px;
}

.login-form-Container > form > div > label {
    font-family: Arial, Helvetica, sans-serif;
    color: rgb(197, 196, 196)
}

.login-form-Container form {
    display: flex;
    flex-direction: column;
    /* justify-content: center !important; */
    margin-top: 20px;
    width: 500px;
}

.loginformContainer form {
    display: flex;
    flex-direction: column;
    /* justify-content: center !important; */
    margin-top: 20px;
    width: 500px;
}


.newUser-input {
    display: flex;
    justify-content: center !important;
    flex-direction: column;
    margin-right: none;
    margin: none;
    width: 90%;
    /* justify-content: center; */
}

.login-errors {
    display: flex;
    flex-direction: column;
    /* justify-content: center; */
    align-items: center;
    color: rgba(253, 52, 52, 0.952);
    margin-top: 10px;
}

.login-errors div {
    margin: 5px;
}

.newUser-input label {
    margin-left: 35px;
    margin-top: 10px;
    margin-bottom: 5px;
}

.addPhoto {
    display: flex;
    width: 450px;
    margin-left: 35px;
}

.addPhoto input {
    cursor: pointer;  
    border: 1px solid rgb(186, 186, 186);
    padding: 6px 12px;
    border-radius: 6px 0px 0px 6px;
    color: rgb(186, 186, 186);
}

.addPhoto input:hover {
    border: 1px solid rgb(116, 114, 114);
}

.addPhoto button {
    cursor: pointer;
    border: none;
    background-color: white;
    padding: 6px 10px;
    border-radius: 0px 6px 6px 0px;
    color: rgb(116, 114, 114);
    width: 114px;
}

.addPhoto button:hover {
    text-decoration: underline;
    
}

.loginInput, 
.signupInput {
    display: flex !important;
    justify-content: center !important;
    padding: 9px 11px;
    border-radius: 5px;
    box-shadow: 0px 0px 2px 1px grey;
    width: 90%;
    border: none;
    height: 20px;
    margin-left: 35px;
}

.loginInput:hover, .signupInput:hover {
    box-shadow: 0px 0px 2px 1px rgba(255, 166, 0, 0.397);
}

.loginButton {
    display: flex;
    justify-content: center;
    text-align: center;
    width: 90%;
    /* margin: 10px 10px 10px -2px; */
    padding: 10px 11px;
    border-radius: 5px;
    border: none;
    height: 40px;
    background-color: #faac18;
    color: white;
    font-size: 16px;
    margin-bottom: 20px;
    margin-top: 30px;
}

.signupBtns {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    text-align: center;
    margin-left: 10px;
    width: 95%;
}

.loginBtns {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    text-align: center;
    margin-left: 10px;
    width: 106%;
}

.login-button {
    /* margin-left: 20px; */
    padding-left: 30px;
    display: flex;
    justify-content: center;
}

.loginButton:hover {
    cursor: pointer;
    box-shadow: 0px 0px 2px 1px rgba(255, 166, 0, 0.397);
    text-decoration: underline;
}

.loginSignupText {
    font-family: Arial, Helvetica, sans-serif;
    color:rgb(238, 227, 227)
}

.loginSignupText > a {
    color:rgb(245, 241, 241)
}

.loginformContainer > form > div > label {
    font-family: Arial, Helvetica, sans-serif;
    color: rgb(197, 196, 196)
}

.signupInput{
    display: flex;
    /* margin: 2px 10px 2px -10px; */
}

#signupButton, #demoButton {
    width: 100.874%;
}


